import { useState } from "react";

const MyBudget = () => {
    const [text, setText] = useState ('');
    const [cat, setCat] = useState ('');
    const [catIn, setCatIn] = useState ('');

    const [groceries,setGroceries] = useState('');
    const [eatingOut,setEatingOut] = useState('');
    const [shopping,setShopping] = useState('');
    const [transport,setTransport] = useState('');
    const [entertainment,setEntertainment] = useState('');
    const [bills,setBills] = useState('');
    const [home,setHome] = useState('');
    const [fees,setFees] = useState('');

    return (
        <div>
        <section>
        <form action="<?=$_SERVER['PHP_SELF']?>" method="post">
            <label for="saving"><b>Input Savings</b></label>
            <input 
                type="text" 
                placeholder="Enter $" 
                name="saving" 
                value={text} 
                onChange={(e) => setText(e.target.value) }/>
          
            <button type="submit">Submit</button>
        </form>
        {/* <!-- need to add js for this button --> */}
        <button onclick="location.href='savings.php';">Check your savings</button>
        </section>
    
        <section>
            {/* <!-- Need to make this visually pretty, table maybe? --> */}
        <form action="budget.php" method="post">
            <b>Input budget</b>
            <label htmlFor="budgetDrop" placeholder="Select Category"></label>
                <select 
                    name="budgetDrop" 
                    id="budgetDrop"
                    value={cat}
                    onClick={(e) => {
                        if(cat === "groceries")
                        {  setCatIn(groceries);}
                        if(cat === "eating_out")
                        { setCatIn(eatingOut);}
                        if(cat === "shopping")
                        { setCatIn(shopping);}
                        if(cat === "transport")
                        { setCatIn(transport);}
                        if(cat === "entertainment")
                        {  setCatIn(entertainment);}
                        if(cat === "bills")
                        {  setCatIn(bills);}
                        if(cat === "home")
                        { setCatIn(home);}
                        if(cat === "fees")
                        { setCatIn(fees); }
                    }}
                    onChange={(e) => 
                    {
                        setCat(e.target.value)
                        
                    }
                    }>
                        <option value="groceries">Groceries</option>
                        <option value="eating_out">Eating Out</option>
                        <option value="shopping">Shopping</option>
                        <option value="transport">Transport</option>
                        <option value="entertainment">Entertainment</option>
                        <option value="bills">Bills</option>
                        <option value="home">Home</option>
                        <option value="fees">Fees</option>
                </select>
            <input type="hidden" name="groceries" value={groceries}/>
            <input type="hidden" name="eating_out" value={eatingOut}/>
            <input type="hidden" name="shopping" value={shopping}/>
            <input type="hidden" name="transport" value={transport}/>
            <input type="hidden" name="entertainment" value={entertainment}/>
            <input type="hidden" name="bills" value={bills}/>
            <input type="hidden" name="home" value={home}/>
            <input type="hidden" name="fees" value={fees}/>
            {console.log(setCat)}
            <label for="saving"><b>Input Amount</b></label>
            <input 
                type="text" 
                placeholder="Enter $" 
                name="saving"
                value={catIn} 
                onFocus={(e) => {
                    if(cat === "groceries")
                    {  setCatIn(groceries);}
                    if(cat === "eating_out")
                    { setCatIn(eatingOut);}
                    if(cat === "shopping")
                    { setCatIn(shopping);}
                    if(cat === "transport")
                    { setCatIn(transport);}
                    if(cat === "entertainment")
                    {  setCatIn(entertainment);}
                    if(cat === "bills")
                    {  setCatIn(bills);}
                    if(cat === "home")
                    { setCatIn(home);}
                    if(cat === "fees")
                    { setCatIn(fees);
                    }}}
                onChange={(e) => {
                if(cat === "groceries")
                { setGroceries(e.target.value); }
                if(cat === "eating_out")
                { setEatingOut(e.target.value); }
                if(cat === "shopping")
                { setShopping(e.target.value); }
                if(cat === "transport")
                { setTransport(e.target.value);}
                if(cat === "entertainment")
                { setEntertainment(e.target.value); }
                if(cat === "bills")
                { setBills(e.target.value);}
                if(cat === "home")
                { setHome(e.target.value);}
                if(cat === "fees")
                { setFees(e.target.value);}
                setCatIn(e.target.value);
                }}/>
            <button type="submit">Submit</button>
        </form>
        </section>
        </div>
    );
}

export default MyBudget 

