import logo from '../official-logo.png';

const Navbar = () => {
    return (
        <nav>
            <img src={logo} alt="logo" id="logo"></img>
            <ul className="links">
                <li><a href='https://www.google.com/'>Home</a></li>
                <li><a href='https://www.google.com/'>My Group</a></li>
            </ul>

            <button className="navButton" id="signout">Sign Out</button>
            <button className="navButton" id="userAccount">User Account</button>
        </nav>
    );
}

export default Navbar